import * as React from "react";
import chroma from "chroma-js";
import { RadialChart, Hint } from "react-vis";
import { colors } from "../../../design-system";
import { isEmpty } from "lodash";
import {
  totalParticipants,
  income,
} from "../../../content/ambassadors-report/metrics";
import Tip from "../../../components/Tip";

interface Props {}
const Income: React.FC<Props> = () => {
  const [value, setValue]: any = React.useState({});
  const colorRange = income.map((_, i) =>
    chroma(colors.green)
      .alpha(1 - 5 * i)
      .css(),
  );

  return (
    <div>
      <p>Income Metrics</p>
      <RadialChart
        colorRange={colorRange}
        animation
        data={income}
        width={300}
        height={300}
        innerRadius={90}
        radius={120}
        showLabels
        labelsStyle={{
          fill: colors.coldWater,
        }}
        onValueMouseOver={(v: any) => {
          setValue({
            data: v,
            title: income.find(item => item.label === v.label).label,
            metrics: [
              {
                label: "Total",
                value: Math.floor(
                  totalParticipants *
                    (income.find(item => item.label === v.label).angle * 0.01),
                ),
              },
              {
                label: "Percentage",
                value: `${income.find(item => item.label === v.label).angle}%`,
              },
            ],
          });
        }}
        onSeriesMouseOut={(v: any) => setValue({})}
      >
        {!isEmpty(value) && (
          <Hint value={value.data}>
            <Tip title={value.title} metrics={value.metrics} />
          </Hint>
        )}
      </RadialChart>
    </div>
  );
};

export default Income;
